<script>
//import API from "../api/API";
export default {
  name: "Mixin",
  data() {
    return {
      //userContent:null,
      userSignInData: {
        user_account: "",
        user_pwd: "",
      },
      userInfo: null,
      TokenID: "",
      Token: "",
      userRequest: this.$axios.create({ baseURL: "../api" }),
      apiToken: (data) => this.userRequest.post("API/Default/Token", data),
    };
  },
  created() {
    // this.getTokenID();
  },
  mounted() {
    if (sessionStorage.getItem("TokenID") != "") {
      this.TokenID = sessionStorage.getItem("TokenID");
    }
  },
  computed: {
    userData() {
      return this.$store.state.userData;
    },
  },
  methods: {
    date2str(date) {
      //西元轉民國
      return [
        date.getFullYear(),
        date.getMonth() + 1 > 9
          ? date.getMonth() + 1
          : "0" + (date.getMonth() + 1),
        date.getDate() > 9 ? date.getDate() : "0" + date.getDate(),
      ];
    },
    FunctionToken: function (EXfunction, data_in) {
      this.apiToken({})
        .then((res) => {
          // data_in.TokenID = this.TokenID;

          let TokenID = this._sessionData("TokenID");
          let TokenID2 =
            "A9207nM9qUOQMqKmaFNxmwdLAxc3WN+W54GRH9EIzjVMNT1kuJoXLzXOnqhYmRu32KRMzdcbanDLYGjTNfEOcw==";
          // console.log(TokenID, TokenID2);
          if (this.IsNull(TokenID) == "") {
            data_in.TokenID = TokenID2;
          } else {
            // console.log(data_in);
            data_in.TokenID = TokenID;
          }
          data_in.Token = res.data;
          data_in.Page = "";

          EXfunction(data_in);
        })
        .catch((error) => {
          alert("發生錯誤FunctionToken!" + error);
        });
    },
    getTokenID() {
      this.$axios
        .post("API/Default/Token")
        .then((res) => {
          this.TokenID = res.data;
          // console.log(res.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    IsNull(data) {
      if (data == "" || data == null || data == undefined) {
        return "";
      } else {
        return data;
      }
    },
  },
};
</script>

