<template>
  <b-container fluid>
    <h2 class="unit-title">抽籤詳細列表顯示介面</h2>
    <div class="mb-3 mt-3 ml-3">
      正取人數：{{ positiveTakeListCount }}人，備取人數：{{
        availableTakeListCount
      }}
    </div>
    <div class="mb-3 mt-3 ml-3">
      <b-button
        id="a"
        variant="primary"
        v-if="availableTakeListCount != 0"
        @click.prevent="GetpositiveTakeList"
        >正取</b-button
      >
      <b-button
        id="b"
        v-if="availableTakeListCount != 0"
        class="ml-3"
        variant="primary"
        @click.prevent="GetavailableTakeList"
        >備取</b-button
      >
      <b-button class="ml-3" variant="primary" @click.prevent="DownloadExcel"
        >下載</b-button
      >
    </div>
    <div class="mb-3 mt-3 ml-3" v-if="availableTakeListCount != 0">
      {{ listName }}
    </div>
    <b-container fluid>
      <b-table
        id="my-table"
        :fields="fields"
        :items="frontDrawlotsList"
        :per-page="perPage"
        :current-page="currentPage"
        small
      >
        <template #cell(AP_ISCOVID19)="data">
          {{ data.item.AP_ISCOVID19 == 0 ? "未接種" : "已接種" }}
        </template>
        <template #cell(AP_ISMARK)="data">
          {{ data.item.AP_ISMARK == 0 ? "否" : "是" }}
        </template>
        <template #cell(AP_ISDRAW)="data">
          {{
            data.item.AP_ISDRAW == 0
              ? "否"
              : data.item.AP_ISDRAW == 1
              ? "正取"
              : "備取"
          }}
        </template>
        <template #cell(AP_ISDRAW_R)="data">
          {{ data.item.AP_ISDRAW_R == 0 ? "否" : "是" }}
        </template>
        <template #cell(AP_DATE)="data">
          {{ parsetime(data.item.AP_DATE) }}
        </template>
      </b-table>

      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
      ></b-pagination>
    </b-container>
  </b-container>
</template>

<script>
export default {
  name: "Admin_Apply",
  data() {
    return {
      perPage: 15,
      currentPage: 1,
      fields: [
        {
          key: "AP_NAME",
          label: "姓名",
        },
        {
          key: "AP_ADDRESS",
          label: "地址",
        },
        {
          key: "AP_PHONE",
          label: "行動電話",
        },
        {
          key: "AP_MAIL",
          label: "電子郵件",
        },
        {
          key: "AP_NID",
          label: "身分證字號",
        },
        {
          key: "AP_CONTACT",
          label: "緊急聯絡人",
        },
        {
          key: "AP_C_PHONE",
          label: "緊急聯絡人電話",
        },
        {
          key: "AP_ISCOVID19",
          label: "接種疫苗",
        },
        {
          key: "AP_ISMARK",
          label: "註記(是否排除資格)",
        },
        {
          key: "AP_ISDRAW",
          label: "是否中籤",
        },
        {
          key: "AP_ISDRAW_R",
          label: "中籤後回覆是否參加",
        },
        {
          key: "AP_DATE",
          label: "報名時間",
        },
      ],
      data: null,
      listName: "正取名單",
      frontDrawlotsList: [], //前端頁面展示用
      positiveTakeList: [], //正取名單
      availableTakeList: [], //備取名單
      positiveTakeListCount: 0, //正取人數
      availableTakeListCount: 0, //備取人數
      dataUrl: null,
      orignDrawlotsList: [], //匯出Excel人數
      apiDrawlots: (data) =>
        this.userRequest.post("API/Default/Drawlots", data),
      // apiDrawlots2: (data) =>
      //   this.userRequest.post("API/Default/Drawlots", data),
    };
  },
  created() {
    this.userSignCheck();
    // this.getList();
  },
  mounted() {
    this.getList();
  },
  computed: {
    rows() {
      return this.frontDrawlotsList.length;
    },
  },
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
      }
    },
    getList() {
      this.data = JSON.parse(this.$route.query.data);
      console.log(this.data);

      this.FunctionToken(this.FunctionGetDrawlots, this.data);

      // if (this.data != null) {
      //   this.FunctionToken(this.FunctionGetDrawlots, this.data);
      // } else {
      //   // this.$router.push({
      //   //   path: "/Admin_DrawLots",
      //   // });
      // }
    },
    GetpositiveTakeList() {
      this.frontDrawlotsList = this.positiveTakeList;
      this.listName = "正取名單";
    },
    GetavailableTakeList() {
      this.frontDrawlotsList = this.availableTakeList;
      this.listName = "備取名單";
    },
    DownloadExcel() {
      window.location = this.dataUrl;
    },
    FunctionGetDrawlots(data_in) {
      this.apiDrawlots(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          //console.log(json);
          if (json.Status) {
            let jsonData = JSON.parse(json.Data);
            this.dataUrl = jsonData.url;
            this.orignDrawlotsList = JSON.parse(jsonData.data);

            var PositiveTake = this.data.PositiveTake;
            var Available = this.data.Available * -1;

            this.positiveTakeList = this.orignDrawlotsList.slice(
              0,
              PositiveTake
            );
            if (Available != 0) {
              this.availableTakeList = this.orignDrawlotsList.slice(Available);
            }
            this.frontDrawlotsList = this.orignDrawlotsList.slice(
              0,
              PositiveTake
            );
            this.positiveTakeListCount = this.positiveTakeList.length;
            this.availableTakeListCount = this.availableTakeList.length;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //轉換時間
    parsetime(date_obj) {
      if (date_obj != null) {
        return date_obj.split("T").join(" ");
      } else {
        return "";
      }
    },
  },
};
</script>