<template>
  <b-container fluid>
    <h2 class="unit-title">發送郵件介面</h2>
    <b-container>
      <b-form class="otherForm increase-block mt-5">
        <b-row>
          <b-col>
            <h5 style="color: red">*請將電子信箱依序填入Excel中</h5>
            <div>上傳檔案格式限定為.csv、.xlsx、.xls</div>
            <div class="row">
              <div style="font-size: 18px" class="pr-3">Example:</div>
              <b-table-simple class="col-sm-1">
                <b-thead>
                  <b-tr>
                    <b-th>電子信箱</b-th>
                  </b-tr>
                </b-thead>
                <b-tbody>
                  <b-tr>
                    <b-td>test1@gmail.com</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>test2@yahoo.com</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>test3@icloud.com</b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </div>
            <input type="file" @change="onChangeP" accept=".csv,.xlsx,.xls" />
            <xlsx-read :file="file">
              <xlsx-json @parsed="getSheet"> </xlsx-json>
            </xlsx-read>
            <b-form-group
              label="郵件主旨"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="text"
                v-model="mailSubject"
                placeholder="請輸入主旨"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group
              label="郵件內容"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-textarea
                id="textarea-rows"
                placeholder="請輸入內容"
                rows="8"
                v-model="mailContent"
              ></b-form-textarea>
            </b-form-group>
            <!-- {{ mailSubject }}
            <div class="pre-line">
              {{ mailContent }}
            </div> -->

            <b-row>
              <b-col offset-lg="2" class="py-3 ml-auto">
                <b-button variant="primary" size="lg" @click="goDialog"
                  >送出</b-button
                >
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
      <div>
        <b-modal
          v-model="dialogShow"
          title="郵件確認"
          ok-only
          ok-title="確定送出"
          size="lg"
          @ok="Import"
        >
          <p class="my-4">郵件主旨：</p>
          <p class="my-4">{{ mailSubject }}</p>
          <p class="my-4">郵件內容：</p>
          <p class="my-4 pre-line">{{ mailContent }}</p>
        </b-modal>
      </div>
    </b-container>
  </b-container>
</template>

<script>
import { XlsxRead, XlsxJson } from "vue-xlsx";
export default {
  components: {
    XlsxRead,
    XlsxJson,
  },
  data() {
    return {
      file: null,
      collection: [],
      mailAddress: [], //電子信箱
      mailSubject: null, //主旨
      mailContent: null, //內容
      dialogShow: false,
      apiSendMail: (data) =>
        this.userRequest.post("API/Default/SendMail", data),
    };
  },
  created() {
    this.userSignCheck();
  },
  computed: {},
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    onChangeP(event) {
      this.mailAddress = [];
      this.file = event.target.files ? event.target.files[0] : null;
    },
    getSheet(val) {
      this.collection = val;
      for (let i = 0; i < this.collection.length; i++) {
        this.mailAddress.push(this.collection[i].電子信箱);
      }
      console.log("mailAddress");
      console.log(this.mailAddress);
    },
    goDialog() {
      if (this.mailSubject == null && this.mailContent == null) {
        alert("主旨或內容不可為空白");
      } else {
        this.dialogShow = true;
      }
    },
    Import() {
      if (this.mailAddress != 0) {
        this.FunctionToken(this.FunctionSendMail, {
          MA_MAIL: this.mailAddress,
          MA_SUBJECT: this.mailSubject,
          MA_CONTENT: this.mailContent.replaceAll("\n", "<br>"),
        });
      } else {
        alert("請上傳收信人Excel");
      }
    },
    //api

    FunctionSendMail(data_in) {
      alert(
        "送信成功，請等待" +
          this.mailAddress.length * 5 +
          "秒後至指定頁面察看結果"
      );
      this.apiSendMail(data_in)
        .then((res) => {
          // let json = JSON.parse(res.data);
          // console.log(json);
          // if (json.Status) {
          //   if (json.Data != "[]") {
          //     console.log(json.Data);
          //     alert("以下電子郵件為錯誤:\n" + json.Data);
          //   } else {
          //     alert("送信成功，請等待!");
          //   }
          //   //this.$router.go();
          // }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
