<template>
  <b-container fluid>
    <h2 class="unit-title">報名人員抽籤介面</h2>
    <b-container>
      <b-form class="otherForm increase-block mt-5">
        <div>目前報名總人數：{{ applyListCount }}人</div>
        <br />
        <b-row>
          <b-col>
            <b-form-group
              label="正取人數"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="number"
                v-model="positiveTake"
                placeholder="請輸入正取人數"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="備取人數"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="number"
                v-model="available"
                placeholder="請輸入備取人數"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="是否納入註記人員"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-checkbox
                name="check-button"
                switch
                size="lg"
                v-model="include"
              ></b-form-checkbox>
            </b-form-group>

            <b-row>
              <b-col offset-lg="2" class="py-3 ml-auto">
                <b-button
                  type="button"
                  variant="primary"
                  size="lg"
                  @click="Export"
                  >抽籤</b-button
                >
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
    </b-container>
  </b-container>
</template>

<script>
export default {
  data() {
    return {
      positiveTake: 0, //正取
      available: 0, //備取
      include: false, //包含註記人員
      applyListCount: 0, //報名人數
      apiGetApply: (data) =>
        this.userRequest.post("API/Default/GetApply", data),
    };
  },
  created() {
    this.userSignCheck();
    this.FunctionToken(this.FunctionGetApply, {});
  },
  computed: {},
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
      }
    },
    Export() {
      var TotalCount = parseInt(this.positiveTake) + parseInt(this.available);
      var CurrentCount = parseInt(this.applyListCount);
      if (this.positiveTake > 0 && TotalCount <= CurrentCount) {
        let json1 = {};
        json1.PositiveTake = ~~this.positiveTake; //正取人數
        json1.Available = ~~this.available; //備取人數
        json1.IsMark = this.include == false ? 0 : 1; //是否包含註記 (0否 : 1是)
        // console.log(json1);
        this.$router.push({
          path: "/Admin_DrawLotsList",
          query: { data: JSON.stringify(json1) },
        });
      } else {
        alert("正取人數不得小於1 或 正備取人數不得大於報名總人數!!");
      }
    },
    //api
    FunctionGetApply(data) {
      this.apiGetApply(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            this.applyListCount = data.length;
          }
          console.log(this.applyListCount);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>