<script>
import NewsListJson from "@/json/NewsList.json";
export default {
  data() {
    return {
      NewsList: NewsListJson,
    };
  },
  created() {
  },
  methods: {
    //跳轉頁面
    goNewsDetail(data) {
      this.$router.push({ path: "/NewsDetail", query: { name: data } });
      //console.log(data);
    },
    dateformatter(item) {
      return this.IsNull(item) == ""
        ? ""
        : item.split("T")[0].replaceAll("-", "/");
    },
  },
};
</script>