<template>
  <div>
    <section class="header-inside"></section>
    <section class="main-content">
      <div class="container">
        <div class="row">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#" @click.prevent="goHome">首頁</a>
              </li>
              <li class="breadcrumb-item">
                <a href="#" @click.prevent="goNews">最新消息</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                {{ NewsList["NE_TITLE"] }}
              </li>
            </ol>
          </nav>
        </div>

        <h2 class="text-center">最新消息</h2>

        <div class="news-content">
          <div class="news-title-info">
            <h3>
              {{ NewsList["NE_TITLE"] }}
            </h3>
            <div class="date">{{ NewsList["NE_DATE"] }}</div>
          </div>
          <div class="news-text">
            {{ DateFormatter(NewsList["NE_CONTENT"]) }}
          </div>
          <div class="py-3 text-center">
            <a
              href="#"
              @click.prevent="goNews"
              class="btn btn-success btn-lg"
              role="button"
              aria-pressed="true"
              >返回最新消息列表</a
            >
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      date: "",
      NewsList: [],
    };
  },
  mounted() {
    this.GetData();
  },
  methods: {
    goHome() {
      this.$router.push({ path: "/" });
    },
    goNews() {
      this.$router.push({ path: "News" });
    },
    //取得資料
    GetData() {
      const NE_ID = this.$route.query.name["NE_ID"];
      console.log(NE_ID);
      if (NE_ID === undefined) {
        this.$router.push({ path: "News" });
      }
      axios.get("../json/NewsDetail/" + NE_ID + ".json").then((response) => {
        console.log(response.data);
        this.NewsList = response.data;
      });
    },
    DateFormatter(item) {
      return this.IsNull(item) == ""
        ? ""
        : item.split("T")[0].replaceAll("-", "/");
    },
  },
};
</script>