<script>
export default {
  name: "Apply",
  data() {
    return {
      //  imageCode: "",
      searchInputText: true,
      inputText: false,
      dialogShow: false, //Dialog隱藏
      dialogLinkShow: false, //DialogLink隱藏
      dialogMessage: "", //彈出視窗文字
      Check_Read: false, //是否閱讀條款
      applyStartTime: "", //報名開始時間
      applyStartTime_Content: "", //報名開始時間-內文
      applyOverTime: "", //報名結束時間
      applyOverTime_Content: "", //報名結束時間-內文
      replyStartTime: "", //回覆開始時間
      replyOverTime: "", //回覆結束時間
      applyOverTimeFront: "", //報名結束時間頁面顯示
      leftTime: "", //剩餘報名時間
      overmessage: 103,
      applyCount: null,
      todayTime: "",
      drawLotsTime: "", //抽籤時間
      searchData: {
        AP_NID: "",
        AP_PHONE: "",
      },
      addData: {
        AP_NAME: "", //姓名
        AP_ADDRESS: "", //地址
        AP_PHONE: "", //行動電話
        AP_MAIL: "", //電子信箱
        AP_NID: "", //身分證字號
        AP_CONTACT: "", //緊急聯絡人
        AP_C_PHONE: "", //緊急聯絡人電話
        AP_ISCOVID19: "0", //接種疫苗
      },
      timeErr: "",
      // apiGetImageCode: (data) =>
      //   this.userRequest.post("API/Default/GetImageCode", data),
      apiGetWebsiteSetting: (data) =>
        this.userRequest.post("API/Default/GetWebsiteSetting", data),
      apiAddApply: (data) =>
        this.userRequest.post("API/Default/AddApply", data),
      apiSelectApply: (data) =>
        this.userRequest.post("API/Default/SelectApply", data),
      apiGetApply: (data) =>
        this.userRequest.post("API/Default/GetApply", data),
    };
  },
  computed: {
    // *********檢查搜尋*************
    //檢查電話
    checkPhone_Search() {
      const re = /^09\d{8}$/;
      return re.test(String(this.searchData.AP_PHONE).toLowerCase());
    },
    // 檢查身份欄位
    checkUserid_Search() {
      const re = /[A-Z][0-9]{9}$/;
      return re.test(String(this.searchData.AP_NID).toUpperCase());
    },
    // *********檢查報名*************
    //檢查電話
    checkPhone() {
      const re = /^09\d{8}$/;
      return re.test(String(this.addData.AP_PHONE).toLowerCase());
    },
    //檢查緊急聯絡人電話
    checkPhone1() {
      const re = /^09\d{8}$/;
      return re.test(String(this.addData.AP_C_PHONE).toLowerCase());
    },
    // 檢查email欄位
    checkEmail() {
      const re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(this.addData.AP_MAIL).toLowerCase());
    },
    // 檢查身份欄位
    checkUserid() {
      const re = /[A-Z][0-9]{9}$/;
      return re.test(String(this.addData.AP_NID).toUpperCase());
    },
  },
  created() {},
  methods: {
    goHome() {
      this.$router.push({ path: "/" });
    },
    goNotice() {
      this.$router.push({ path: "/Apply_Notice" });
      // this.$router.push({ path: "/Apply_Notice" ,query:{applyStartTime_Content: this.applyStartTime_Content , applyOverTime_Content:this.applyOverTime_Content}});
    },
    goReply() {
      this.$router.push({ path: "/Reply" });
    },
    // *********查詢報名狀況*************
    Search_Info() {
      this.errorText = "";
      if (this.searchData.AP_NID == "") this.errorText += "請輸入身分證號\n";
      else if (!this.checkUserid_Search)
        this.errorText += "請輸入正確身份證號(英文請寫大寫)\n";
      if (this.searchData.AP_PHONE == "") this.errorText += "請輸入行動電話 \n";
      else if (!this.checkPhone_Search)
        this.errorText += "請輸入正確行動電話\n";

      if (this.errorText !== "") alert(this.errorText);
      else {
        this.FunctionToken(this.FunctionSelectApply, this.searchData);
      }
    },
    // *********新增報名*************
    //報名檢查
    checkInput() {
      this.errorText = "";
      if (this.addData.AP_NAME == "") this.errorText += "請輸入姓名\n";
      if (this.addData.AP_ADDRESS == "") this.errorText += "請輸入地址\n";
      if (this.addData.AP_PHONE == "") this.errorText += "請輸入行動電話 \n";
      else if (!this.checkPhone) this.errorText += "請輸入正確行動電話\n";

      if (this.addData.AP_MAIL == "") this.errorText += "請輸入電子郵件 \n";
      else if (!this.checkEmail) this.errorText += "請輸入正確電子郵件\n";

      if (this.addData.AP_NID == "") this.errorText += "請輸入身分證號 \n";
      else if (!this.checkUserid)
        this.errorText += "請輸入正確身份證號(英文請寫大寫)\n";

      if (this.addData.AP_CONTACT == "") this.errorText += "請輸入緊急聯絡人\n";

      if (this.addData.AP_C_PHONE == "")
        this.errorText += "請輸入緊急聯絡人電話\n";
      else if (!this.checkPhone1)
        this.errorText += "請輸入正確緊急聯絡人電話\n";

      if (this.Check_Read != true)
        this.errorText += "請閱讀並同意隱私權保護政策";

      if (this.errorText !== "") alert(this.errorText);
      //else if (this.addData.AP_ISCOVID19 == 0) alert("如無接種疫苗則無法報名!");
    },
    //進入報名
    addRegdet() {
      if (this.timeErr === "") {
        this.checkInput();
        // if (this.errorText === "" && this.addData.AP_ISCOVID19 == 1) {
        if (this.errorText === "") {
          this.FunctionToken(this.FunctionAddApply, this.addData);
        } else {
          console.log("報名失敗");
        }
      } else {
        this.checkTime();
      }
    },
    checkTime() {
      var today = new Date();
      this.todayTime = today;
      var applyStartTime_chk = new Date(this.applyStartTime);
      var applyOverTime_chk = new Date(this.applyOverTime);

      //距離報名截止剩餘時間
      var days = (applyOverTime_chk - today) / 1000 / 3600 / 24;
      var day = Math.floor(days);
      var hours = (days - day) * 24;
      var hour = Math.floor(hours);
      var mins = (hours - hour) * 60;
      var min = Math.floor(mins);
      if (day >= 0) {
        this.leftTime = "還有" + day + "天" + hour + "時" + min + "分";
      } else {
        this.overmessage = "";
      }

      //報名開始+結束時間
      var weekArray = new Array(
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六"
      );
      var applyStart = this.applyStartTime;
      var applyStartWeek = weekArray[new Date(applyStartTime_chk).getDay()]; //取得星期
      this.applyStartTime_Content =
        applyStart.substring(0, 4) -
        1911 +
        "年" +
        applyStart.substring(5, 7) +
        "月" +
        applyStart.substring(8, 10) +
        "日(" +
        applyStartWeek +
        ")" +
        applyStart.substring(11, 13) +
        "時";
      var applyOver = this.applyOverTime;
      var applyOverWeek = weekArray[new Date(applyOverTime_chk).getDay()];
      this.applyOverTime_Content =
        applyOver.substring(0, 4) -
        1911 +
        "年" +
        applyOver.substring(5, 7) +
        "月" +
        applyOver.substring(8, 10) +
        "日(" +
        applyOverWeek +
        ")" +
        applyOver.substring(11, 13) +
        "時";

      //判斷時間
      var Err = "";
      if (
        Date.parse(today).valueOf() < Date.parse(applyStartTime_chk).valueOf()
      ) {
        this.searchInputText = false;
        alert("尚未開放報名！");
        Err = "Err";
      } else if (
        Date.parse(today).valueOf() > Date.parse(applyOverTime_chk).valueOf()
      ) {
        alert("報名已截止！");
        Err = "Err";
      } else {
        this.inputText = true;
      }
      this.timeErr = Err;
    },
