    FunctionAddApply(data_in) {
      this.apiAddApply(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            //let data = json.Data;
            //console.log(data);
            alert("報名成功!");
            this.$router.go();
          } else {
            //let data = json.Data;
            alert(json.Data);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    FunctionSelectApply(data_in) {
      this.dialogLinkShow = false; //初始值
      this.apiSelectApply(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            //console.log(data);

            //報名結束時間
            let dateApplyOver = this.applyOverTime;
            let ApplyOverYear = dateApplyOver.substring(0, 4);
            let ApplyOverMonth = dateApplyOver.substring(5, 7);
            let ApplyOverDay = dateApplyOver.substring(8, 10);
            let ApplyOverDate = `${ApplyOverYear}年${ApplyOverMonth}月${ApplyOverDay}日`;

            //抽籤時間
            let dateDrawLots = this.drawLotsTime;
            let DrawLotsYear = dateDrawLots.substring(0, 4);
            let DrawLotsMonth = dateDrawLots.substring(5, 7);
            let DrawLotsDay = dateDrawLots.substring(8, 10);
            let DrawLotsDate = `${DrawLotsYear}年${DrawLotsMonth}月${DrawLotsDay}日`;

            //回覆開始時間
            let dateRreplyStart = this.replyStartTime;
            let RreplyStartYear = dateRreplyStart.substring(0, 4);
            let RreplyStartMonth = dateRreplyStart.substring(5, 7);
            let RreplyStartDay = dateRreplyStart.substring(8, 10);
            let RreplyStartDate = `${RreplyStartYear}年${RreplyStartMonth}月${RreplyStartDay}日`;

            //回覆結束時間
            let dateReplyOver = this.replyOverTime;
            let RreplyOverYear = dateReplyOver.substring(0, 4);
            let RreplyOverMonth = dateReplyOver.substring(5, 7);
            let RreplyOverDay = dateReplyOver.substring(8, 10);
            let RreplyOverDate = `${RreplyOverYear}年${RreplyOverMonth}月${RreplyOverDay}日`;

            var applyStartTime_chk = new Date(this.applyStartTime);
            var applyOverTime_chk = new Date(this.applyOverTime);
            var replyStartTime_chk = new Date(this.replyStartTime);
            var replyOverTime_chk = new Date(this.replyOverTime);
            var drawLotsTime_chk = new Date(this.drawLotsTime);
            //報名時間結束前查詢:
            if (
              Date.parse(this.todayTime).valueOf() <
              Date.parse(applyOverTime_chk).valueOf()
            ) {
              //alert("報名開放期間");
              if (data === null) {
                this.dialogMessage =
                  "您尚未報名，請於" + ApplyOverDate + "前完成報名。";
              } else {
                this.dialogMessage =
                  "您已報名成功，本會將於" +
                  DrawLotsDate +
                  "抽籤，祝您幸運中籤。";
              }
              this.dialogShow = true;
            }
            //報名開放期間結束後~抽籤時間
            else if (
              Date.parse(this.todayTime).valueOf() >
                Date.parse(applyOverTime_chk).valueOf() &&
              Date.parse(this.todayTime).valueOf() <
                Date.parse(drawLotsTime_chk).valueOf()
            ) {
              if (data === null) {
                this.dialogMessage = "報名時間已截止，查無您的報名資料。";
              } else {
                this.dialogMessage =
                  "報名時間已截止，您已報名成功，本會將於" +
                  DrawLotsDate +
                  "抽籤，祝您幸運中籤。";
              }
              this.dialogShow = true;
            }
            //抽籤時間~回覆結束時間
            else if (
              Date.parse(this.todayTime).valueOf() >
                Date.parse(drawLotsTime_chk).valueOf() &&
              Date.parse(this.todayTime).valueOf() <
                Date.parse(replyOverTime_chk).valueOf()
            ) {
              if (data === null) {
                this.dialogMessage = "報名時間已截止，查無您的報名資料。";
              }
              //未中籤
              else if (data.AP_ISDRAW == 0) {
                this.dialogMessage = "您沒有中籤";
              }
              //備取
              else if (data.AP_ISDRAW == 2) {
                this.dialogMessage = "您為備取第" + data.AP_AVAILABLENUM + "位";
              }
              //正取 未回覆
              else if (data.AP_ISDRAW == 1 && data.AP_ISDRAW_R == 0) {
                this.dialogMessage =
                  "恭喜您已中籤，請於" +
                  RreplyStartDate +
                  "至" +
                  RreplyOverDate +
                  "內，至下方連結回覆您是否參加。";
                this.dialogLinkShow = true;
              }
              //正取 已回覆
              else if (data.AP_ISDRAW == 1 && data.AP_ISDRAW_R == 1) {
                this.dialogMessage =
                  "恭喜您已中籤，已收到您回覆將出席參加國慶活動。";
              }
              //第二次抽籤正取未回覆
              else if (data.AP_ISDRAW == 3) {
                this.dialogMessage =
                  "恭喜您已中籤，因未收到您回覆參加，名額已讓給備取。";
              }
              this.dialogShow = true;
            }
            //中籤回覆時間後
            else if (
              Date.parse(this.todayTime).valueOf() >
              Date.parse(replyOverTime_chk).valueOf()
            ) {
              if (data === null) {
                this.dialogMessage = "報名時間已截止，查無您的報名資料。";
              }
              //未中籤
              else if (data.AP_ISDRAW == 0 && data.AP_ISDRAW_R == 0) {
                this.dialogMessage = "您沒有中籤。";
              }
              //備取
              else if (data.AP_ISDRAW == 2) {
                this.dialogMessage = "您為備取第" + data.AP_AVAILABLENUM + "位";
              }
              //正取 已回覆
              else if (data.AP_ISDRAW == 1 && data.AP_ISDRAW_R == 1) {
                this.dialogMessage =
                  "恭喜您已中籤，已收到您回覆將出席參加國慶活動。";
              }
              //正取 未回覆
              else if (data.AP_ISDRAW == 1 && data.AP_ISDRAW_R == 0) {
                this.dialogMessage =
                  "恭喜您已中籤，因未收到您回覆參加，名額已讓給備取。";
              }
              //第二次抽籤正取未回覆
              else if (data.AP_ISDRAW == 3) {
                this.dialogMessage =
                  "恭喜您已中籤，因未收到您回覆參加，名額已讓給備取。";
              }
              this.dialogShow = true;
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    FunctionGetWebsiteSetting(data) {
      this.apiGetWebsiteSetting(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            //console.log(data);
            this.applyStartTime = data.WS_VALUE1;
            this.applyOverTime = data.WS_VALUE2;
            this.replyStartTime = data.WS_VALUE3;
            this.replyOverTime = data.WS_VALUE4;
            this.drawLotsTime = data.WS_VALUE5;
            //console.log(this.applyStartTime);
            var overTime = data.WS_VALUE2.substring(5, 16);
            var month = overTime.substring(0, 2);
            var day = overTime.substring(3, 5);
            var hour = overTime.substring(6, 8);
            var min = overTime.substring(9, 11);
            this.applyOverTimeFront =
              month + "月" + day + "日" + hour + "時" + min + "分";
            this.checkTime(); //檢查時間
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    FunctionGetApply(data) {
      (this.isLoading = true),
        this.apiGetApply(data)
          .then((res) => {
            let json = JSON.parse(res.data);
            if (json.Status) {
              this.isLoading = false;
              let data = JSON.parse(json.Data);
              this.applyCount = data.length;
            }
          })
          .catch((err) => {
            console.log(err);
          });
    },
  },
};
</script>