<template>
  <b-container fluid>
    <loading :active.sync="isLoading"></loading>
    <h2 class="unit-title">已寄送郵件清單介面</h2>
    <div class="mb-3 mt-3 ml-3">
      狀態
      <b-form-select
        v-model="selected"
        class="col-sm-1"
        :options="options"
      ></b-form-select>
      <b-button variant="danger" @click.prevent="getDrawLotsList"
        >搜尋</b-button
      >
    </div>
    <div class="mb-3 mt-3 ml-3">目前表單共：{{ mailListCount }} 筆資料</div>
    <b-container fluid>
      <b-table
        id="my-table"
        :fields="fields"
        :items="mailList"
        :per-page="perPage"
        :current-page="currentPage"
        small
      >
      </b-table>
      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
      ></b-pagination>
    </b-container>
  </b-container>
</template>

<script>
export default {
  name: "Admin_Apply",
  data() {
    return {
      drawLotsText: "全部",
      drawLotsReplyText: "全部",
      perPage: 15,
      currentPage: 1,
      fields: [
        {
          key: "MA_MAIL",
          label: "電子信箱",
        },
        {
          key: "MA_STATUS",
          label: "狀態",
        },
        {
          key: "MA_SUBJECT",
          label: "主旨",
        },
        // {
        //   key: "MA_CONTENT",
        //   label: "內容",
        // },
        {
          key: "MA_DATE",
          label: "發送時間",
        },
      ],
      selected: null,
      options: [
        { value: null, text: "全部" },
        { value: 0, text: "成功" },
        { value: 1, text: "失敗" },
      ],
      isLoading: false,
      mailList: [],
      mailListCount: 0,
      orignMailList: [],
      apiGetMailList: (data) =>
        this.userRequest.post("API/Default/GetMailList", data),
    };
  },
  created() {
    this.userSignCheck();
    this.FunctionToken(this.GetMailList, {});
  },
  computed: {
    rows() {
      return this.mailList.length;
    },
  },
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    getDrawLotsList() {
      this.mailList = [];
      //console.log(this.selected);
      if (this.selected == null) {
        this.mailList = this.orignMailList;
      } else if (this.selected == 0) {
        for (let i in this.orignMailList) {
          let subitem = this.orignMailList[i];
          if (subitem.MA_STATUS == '成功')
            this.mailList.push(subitem);
        }
      }else{
        for (let i in this.orignMailList) {
          let subitem = this.orignMailList[i];
          if (subitem.MA_STATUS != '成功')
            this.mailList.push(subitem);
        }
      }
      
      this.mailListCount = this.mailList.length;
    },
    //api
    GetMailList(data) {
      this.isLoading = true;
        this.apiGetMailList(data)
          .then((res) => {
            let json = JSON.parse(res.data);
            if (json.Status) {
              this.isLoading = false;
              let data = JSON.parse(json.Data);
              this.mailList = data;
              this.orignMailList = data;
              this.mailListCount = data.length;
            }
          })
          .catch((err) => {
            console.log(err);
          });
    },
  },
};
</script>