<template>
  <b-container fluid>
    <loading :active.sync="isLoading"></loading>
    <h2 class="unit-title">最新消息管理介面</h2>

    <!-- <b-container fluid> -->
    <b-container>
      <b-row>
        <b-col class="text-right">
          <a href="#" class="button" @click.prevent="AddNews">新增</a>
        </b-col>
      </b-row>
    </b-container>

    <b-container>
      <b-table-simple responsive>
        <b-thead>
          <b-tr>
            <b-th>標題</b-th>
            <b-th>狀態</b-th>
            <b-th>發布日期</b-th>
            <b-th>修改</b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr v-for="(item, i) in NewsList.items" :key="i">
            <b-td>{{ item["NE_TITLE"] }}</b-td>
            <b-td>{{ item["NE_ISONLINE"] == 0 ? "下線" : "上線" }}</b-td>
            <b-td>{{ dateformatter(item["NE_DATE"]) }}</b-td>
            <b-td>
              <b-button variant="info" @click="DeleteNews(item)">刪除</b-button>
              <b-button class="ml-5" variant="info" @click="EditNews(item)"
                >編輯</b-button
              >
            </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
    </b-container>
  </b-container>
</template>

<script>
export default {
  name: "HeadMenu",
  data() {
    return {
      isLoading:false,
      NewsList: {
        items: [],
      },
      apiGetNews: (data) => this.userRequest.post("API/Default/GetNews", data),
      apiDeleteNews: (data) =>
        this.userRequest.post("API/Default/DelectNews", data),
    };
  },
  created() {
    this.userSignCheck();
    this.FunctionToken(this.GetNews, {});
  },
  methods: {
    dateformatter(item) {
      return this.IsNull(item) == ""
        ? ""
        : item.split("T")[0].replaceAll("-", "/");
    },
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      } else {
        this.$router.push({ path: "/Admin_Index" });
      }
    },
    
    AddNews() {
      this.$router.push({ path: "/Admin_Add_News" });
    },
    DeleteNews(item) {
      //console.log(item);
      if (confirm("確定要刪除" + this.IsNull(item.NE_TITLE) + "嗎?") == false) {
        return;
      }
      //console.log(item.NE_ID);
      this.FunctionToken(this.DelectNews, {NE_ID: item.NE_ID});
    },
    EditNews(data) {
      this.$router.push({ path: "/Admin_Edit_News", query: { name: data } });
    },
    //===========================================================
    //api
    GetNews(data) {
      this.isLoading = true;
      this.apiGetNews(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            this.isLoading = false;
            let data = JSON.parse(json.Data);
            this.NewsList.items = data;
            //console.log(this.NewsList.items["NE_ISONLINE"]);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    DelectNews(data_in) {
      this.apiDeleteNews(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            alert("刪除成功!");
            this.$router.go();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>