import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import index from "../views/index.vue";
import index_admin from "../views/Admin/index_admin";

//前台
import Home from '../views/Home'
import Apply from '../views/Apply'
import Apply_Notice from '../views/Apply_Notice'
import News from '../views/News'
import NewsDetail from '../views/NewsDetail'
import Announcement from '../views/Announcement'
//後台
import Admin_Apply from '../views/Admin/Admin_Apply'
import Admin_Index from '../views/Admin/Admin_Index'
import Admin_Import_Apply from '../views/Admin/Admin_Import_Apply'
import Admin_DrawLots from '../views/Admin/Admin_DrawLots'
import Admin_TimeSet from '../views/Admin/Admin_TimeSet'
import Admin_UpdateApply from '../views/Admin/Admin_UpdateApply'
import Admin_DrawLotsList from '../views/Admin/Admin_DrawLotsList'
import Admin_SendMail from '../views/Admin/Admin_SendMail'
import Admin_MailList from '../views/Admin/Admin_MailList'
//excel套件
import JsonExcel from "vue-json-excel";

//防止路由重複讀取錯誤
//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const scrollBehavior = function (to, from, savedPosition) {
  // savedPosition 会在你使用浏览器前进或后退按钮时候生效
  // 这个跟你使用 router.go() 或 router.back() 效果一致
  // 这也是为什么我在 tab 栏结构中放入了一个 点击回退 的按钮
  if (savedPosition) {
    return savedPosition
  } else {
    // 如果不是通过上述行为切换组件，就会让页面回到顶部
    return { x: 0, y: 0 }
  }
}

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: index,
    children: [
      {
        path: "Login",
        name: "Login",
        component: Login,
      },
      {
        path: "",
        name: "Home",
        component: Home,
      },
      {
        path: 'Apply',
        name: 'Apply',
        component: Apply
      },
      {
        path: 'Apply_Notice',
        name: 'Apply_Notice',
        component: Apply_Notice
      },
      {
        path: 'News',
        name: 'News',
        component: News
      },
      {
        path: 'NewsDetail',
        name: 'NewsDetail',
        component: NewsDetail
      },
      {
        path: 'Announcement',
        name: 'Announcement',
        component: Announcement
      }
    ],
  },
  {
    path: "/index_admin",
    component: index_admin,
    children: [
      {
        path: "/Admin_Apply",
        name: "Admin_Apply",
        component: Admin_Apply,
      },
      {
        path: "/Admin_Index",
        name: "Admin_Index",
        component: Admin_Index,
      },
      {
        path: "/Admin_Import_Apply",
        name: "Admin_Import_Apply",
        component: Admin_Import_Apply,
      },
      {
        path: "/Admin_DrawLots",
        name: "Admin_DrawLots",
        component: Admin_DrawLots,
      },
      {
        path: "/Admin_TimeSet",
        name: "Admin_TimeSet",
        component: Admin_TimeSet,
      },
      {
        path: "/Admin_UpdateApply",
        name: "Admin_UpdateApply",
        component: Admin_UpdateApply,
      },
      {
        path: "/Admin_DrawLotsList",
        name: "Admin_DrawLotsList",
        component: Admin_DrawLotsList,
      },
      {
        path: "/Admin_SendMail",
        name: "Admin_SendMail",
        component: Admin_SendMail,
      },
      {
        path: "/Admin_MailList",
        name: "Admin_MailList",
        component: Admin_MailList,
      }
    ],
  }
];



const router = new VueRouter({
  //mode: 'history',
  //base: process.env.BASE_URL,
  scrollBehavior,
  routes,
});

export default router;
