<template>
  <b-container fluid>
    <h2 class="unit-title">前台時間設定介面</h2>
    <b-container>
      <b-form class="otherForm increase-block mt-5">
        <b-row>
          <b-col>
            <b-form-group
              label="報名開放時間"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="datetime-local"
                v-model="applyStartTime"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="報名結束時間"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="datetime-local"
                v-model="applyOverTime"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="抽籤時間"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="datetime-local"
                v-model="drawLotsTime"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="中籤回覆開放時間"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="datetime-local"
                v-model="replyStartTime"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group
              label="中籤回覆結束時間"
              label-class="main-title"
              label-cols-lg="2"
            >
              <b-form-input
                id="input-1"
                type="datetime-local"
                v-model="replyOverTime"
                required
              ></b-form-input>
            </b-form-group>

            <b-row>
              <b-col offset-lg="2" class="py-3 ml-auto">
                <b-button variant="primary" size="lg" @click="Save"
                  >確定</b-button
                >
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
    </b-container>
  </b-container>
</template>

<script>
export default {
  data() {
    return {
      applyStartTime: "", //報名開始時間
      applyOverTime: "", //報名結束時間
      replyStartTime: "", //回覆開始時間
      replyOverTime: "", //回覆結束時間
      drawLotsTime: "", //抽籤時間
      apiGetWebsiteSetting: (data) =>
        this.userRequest.post("API/Default/GetWebsiteSetting", data),
      apiUpdateWebsiteSetting: (data) =>
        this.userRequest.post("API/Default/UpdateWebsiteSetting", data),
    };
  },
  created() {
    this.userSignCheck();
    this.FunctionToken(this.GetWebsiteSetting, {});
  },
  computed: {},
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
      }
    },
    Save() {
      this.errorText = "";
      if (this.applyStartTime == "") this.errorText += "請輸入報名開始時間\n";
      if (this.applyOverTime == "") this.errorText += "請輸入報名結束時間\n";
      if (this.replyStartTime == "") this.errorText += "請輸入查詢開始時間\n";
      if (this.replyOverTime == "") this.errorText += "請輸入查詢結束時間\n";
      if (this.drawLotsTime == "") this.errorText += "請輸入抽籤時間\n";
      if (
        Date.parse(this.applyStartTime).valueOf() >
        Date.parse(this.applyOverTime).valueOf()
      )
        this.errorText += "報名開始時間不得早於結束時間\n";
        if (
        Date.parse(this.drawLotsTime).valueOf() <
        Date.parse(this.applyOverTime).valueOf()
      )
        this.errorText += "抽籤時間不得早於報名結束時間\n";
      if (
        Date.parse(this.drawLotsTime).valueOf() >
        Date.parse(this.replyStartTime).valueOf()
      )
        this.errorText += "中籤回復開放查詢時間不得早於抽籤時間\n";
      if (
        Date.parse(this.replyStartTime).valueOf() >
        Date.parse(this.replyOverTime).valueOf()
      )
        this.errorText += "中籤回復開放查詢時間不得早於結束時間\n";
      if (
        Date.parse(this.applyOverTime).valueOf() >
        Date.parse(this.replyStartTime).valueOf()
      )
        this.errorText += "中籤回復開放查詢時間不得早於報名結束時間\n";
      if (this.errorText !== "") alert(this.errorText);
      else {
        let json = {};
        json.WS_VALUE1 = this.applyStartTime;
        json.WS_VALUE2 = this.applyOverTime;
        json.WS_VALUE3 = this.replyStartTime;
        json.WS_VALUE4 = this.replyOverTime;
        json.WS_VALUE5 = this.drawLotsTime;
        //console.log(json);
        this.FunctionToken(this.FunctionUpdateWebsiteSetting, json);
      }
    },
    //api
    GetWebsiteSetting(data) {
      this.apiGetWebsiteSetting(data)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            let data = JSON.parse(json.Data);
            //console.log(data)
            this.applyStartTime = data.WS_VALUE1;
            this.applyOverTime = data.WS_VALUE2;
            this.replyStartTime = data.WS_VALUE3;
            this.replyOverTime = data.WS_VALUE4;
            this.drawLotsTime = data.WS_VALUE5;
            //console.log( this.applyStartTime);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    FunctionUpdateWebsiteSetting(data_in) {
      this.apiUpdateWebsiteSetting(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          if (json.Status) {
            alert("更新成功!");
            this.$router.go();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>