<template>
<div>
  <section class="header-inside"></section>
  <b-container align="center">
    <loading :active.sync="isLoading"></loading>
    <b-form style="width: 50%" @submit="userLogin" class="mt-5">
      <b-form-group label="帳號" label-for="username" class="text-left">
        <b-form-input
          id="username"
          type="text"
          placeholder="請輸入帳號"
          v-model="loginData.Id"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group label="密碼" label-for="password" class="text-left">
        <b-form-input
          id="password"
          type="password"
          placeholder="請輸入密碼"
          v-model="loginData.DecryptionPass"
          required
        ></b-form-input>
      </b-form-group>

      <!-- <b-form-group label="圖形驗證碼" label-for="code" class="text-left">
        <b-form-input
          id="code"
          type="text"
          placeholder="請輸入圖形驗證碼"
          v-model="loginData.CodePass"
          required
        ></b-form-input>
      </b-form-group> -->

      <!-- <img width="100%" :src="imageCode" class="mb-3" /> -->

      <b-container>
        <b-row>
          <b-col
            ><b-button
              @click.prevent="userLogin"
              type="submit"
              variant="success"
              >登入</b-button
            ></b-col
          >
        </b-row>
      </b-container>
    </b-form>
  </b-container>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      isLoading: false,
      imageCode: "",
      loginData: {
        Id: "",
        DecryptionPass: "",
        CodePass: "",
        DecryptionCodePass: "",
        Type: "login",
        TokenID: "",
        Token: "",
        Page: "",
      },
      lastpass: "",
      checkA: false,
      errormsg: "",
      apiGetImageCode: (data) =>
        this.userRequest.post("API/Default/GetImageCode", data),
      apiLogin: (data) => this.userRequest.post("API/Default/Login", data),
      apiLastPass: (data) =>
        this.userRequest.post("API/Default/LastPass", data),
    };
  },
  created() {
    // this.getImageCode();
  },
  methods: {
    goIndex() {
      this.$router.push({ path: "/" });
    },
    getImageCode() {
      this.apiGetImageCode()
        .then((res) => {
          const json = JSON.parse(res.data);
          if (json.Status) {
            const jsonImage = JSON.parse(json.Data);
            this.imageCode = jsonImage.Image;
            this.loginData.DecryptionCodePass = jsonImage.Code;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    checkAchange() {
      if (!this.checkA) {
        sessionStorage.removeItem("CCC");
      }
      sessionStorage.setItem("checkA", this.checkA);
    },
    Login(data) {
      this.apiLogin(data)
        .then((res) => {
          const json = JSON.parse(res.data);
          if (json.Status) {
            const jsonToken = JSON.parse(json.Data);
            sessionStorage.setItem("TokenID", jsonToken);
            if (this.checkA) {
              sessionStorage.setItem("CCC", data.Id);
            } else {
              sessionStorage.setItem("CCC", null);
            }
            sessionStorage.setItem("PM_TYPE", 0);
            this.$router.push({ path: "/Admin_index" });
          } else {
            alert(json.Message);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    userLogin() {
      this.checkLoginData();
      if (this.errormsg === "") {
        // this.FunctionToken(this.Login, this.loginData);
        if(this.loginData.Id == "eric" && this.loginData.DecryptionPass=="!test123"){
  // const jsonToken = JSON.parse(json.Data);
            sessionStorage.setItem("TokenID", "TokenID");
            if (this.checkA) {
              sessionStorage.setItem("CCC", "CCC");
            } else {
              sessionStorage.setItem("CCC", null);
            }
            sessionStorage.setItem("PM_TYPE", 0);
            this.$router.push({ path: "/Admin_index" });
        }
      }
    },
    checkLoginData() {
      this.errormsg = "";
      if (this.loginData.Id == "") this.errormsg += "\n請輸入帳號!!";
      if (this.loginData.DecryptionPass == "")
        this.errormsg += "\n請輸入密碼!!";
      // if (this.loginData.CodePass == "")
      //   this.errormsg += "\n請輸入正確的圖形驗證碼!!";
      if (this.errormsg != "") alert(this.errormsg);
    }
  },
};
</script>
