            <div class="row">
              <div style="font-size: 18px" class="pr-3">Example:</div>
              <b-table-simple class="col-sm-1">
                <b-thead>
                  <b-tr>
                    <b-th>正取</b-th>
                    <b-th>備取</b-th>
                    <b-th>正取未回覆</b-th>
                  </b-tr>
                </b-thead>
                <b-tbody>
                  <b-tr>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                    <b-td>APtZJ0ZAIit0aszTicdO7g</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>APtZJ0ZAIit0aszTicdO7g</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>APto6ddZaTvECe3TzH2IVmoQ</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>APwnTcQWbfk0u1OOSPWMO4g</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                    <b-td>APrSxoEWJAX0Cr2s2h2vbTfA</b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </div>
            <input type="file" @change="onChangeP" accept=".csv,.xlsx,.xls" />
