<script>
import { XlsxRead, XlsxJson } from "vue-xlsx";
export default {
  components: {
    XlsxRead,
    XlsxJson,
  },
  data() {
    return {
      file: null,
      collection: [],
      postiveTake: [], //正取名單
      available: [], //備取名單
      postiveTakeNoReply: [], //正取未回覆名單
      apiUpdateApplyByDrawlots: (data) =>
        this.userRequest.post("API/Default/UpdateApplyByDrawlots", data),
    };
  },
  created() {
    this.userSignCheck();
  },
  computed: {},
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    onChangeP(event) {
      this.postiveTake = [];
      this.available = [];
      this.postiveTakeNoReply = [];
      this.file = event.target.files ? event.target.files[0] : null;
    },
    getSheet(val) {
      this.collection = val;
      for (let i = 0; i < this.collection.length; i++) {
        this.postiveTake.push(this.collection[i].正取);
        this.available.push(this.collection[i].備取);
        this.postiveTakeNoReply.push(this.collection[i].正取未回覆)
      }
      console.log(this.postiveTake);
      console.log(this.available);
      console.log(this.postiveTakeNoReply);
    },
    Import() {
      this.FunctionToken(this.FunctionUpdateApplyByDrawlots, {
        AP_ID_P: this.postiveTake,
        AP_ID_A: this.available,
        AP_ID_NOREPLY :this.postiveTakeNoReply,
      });
    },
    //api

    FunctionUpdateApplyByDrawlots(data_in) {
      this.apiUpdateApplyByDrawlots(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          console.log(json);
          if (json.Status) {
            if (json.Data != "[]") {
              console.log(json.Data);
              alert("以下報名編號為錯誤:\n" + json.Data);
            } else {
              alert("更改成功!");
            }

            //this.$router.go();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>