<template>
  <b-container fluid>
    <loading :active.sync="isLoading"></loading>
    <h2 class="unit-title">報名人員顯示介面</h2>
    <div class="mb-3 mt-3 ml-3">
      是否中籤
      <b-form-select
        v-model="selected1"
        class="col-sm-1"
        :options="options1"
      ></b-form-select>
      中籤後回覆是否參加
      <b-form-select
        v-model="selected2"
        class="col-sm-1"
        :options="options2"
      ></b-form-select>
      <b-button variant="danger" @click.prevent="getDrawLotsList"
        >搜尋</b-button
      >
    </div>
    <div class="mb-3 mt-3 ml-3" >
      目前表單人數：{{applyListCount}}人
      </div>
    <b-container fluid>
      <b-table
        id="my-table"
        :fields="fields"
        :items="applyList"
        :per-page="perPage"
        :current-page="currentPage"
        small
      >
        <template #cell(AP_ISCOVID19)="data">
          {{ data.item.AP_ISCOVID19 == 0 ? "未接種" : "已接種" }}
        </template>
        <template #cell(AP_ISMARK)="data">
          {{ data.item.AP_ISMARK == 0 ? "否" : "是" }}
        </template>
        <template #cell(AP_ISDRAW)="data">
          {{
            data.item.AP_ISDRAW == 0
              ? "否"
              : data.item.AP_ISDRAW == 1
              ? "正取"
              : data.item.AP_ISDRAW == 2
              ? "備取"
              : "正取未回覆"
          }}
        </template>
        <template #cell(AP_ISDRAW_R)="data">
          {{ data.item.AP_ISDRAW_R == 0 ? "否" : "是" }}
        </template>
        <template #cell(AP_DATE)="data">
          {{ parsetime(data.item.AP_DATE) }}
        </template>
        <template #cell(AP_REPLYDATE)="data">
          {{ parsetime(data.item.AP_REPLYDATE) }}
        </template>
      </b-table>

      <b-pagination
        v-model="currentPage"
        :total-rows="rows"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
      ></b-pagination>
    </b-container>
  </b-container>
</template>

<script>
export default {
  name: "Admin_Apply",
  data() {
    return {
      drawLotsText: "全部",
      drawLotsReplyText: "全部",
      perPage: 15,
      currentPage: 1,
      fields: [
        {
          key: "AP_NAME",
          label: "姓名",
        },
        {
          key: "AP_ADDRESS",
          label: "地址",
        },
        {
          key: "AP_PHONE",
          label: "行動電話",
        },
        {
          key: "AP_MAIL",
          label: "電子郵件",
        },
        {
          key: "AP_NID",
          label: "身分證字號",
        },
        {
          key: "AP_CONTACT",
          label: "緊急聯絡人",
        },
        {
          key: "AP_C_PHONE",
          label: "緊急聯絡人電話",
        },
        {
          key: "AP_ISMARK",
          label: "註記",
        },
        {
          key: "AP_ISDRAW",
          label: "是否中籤",
        },
        {
          key: "AP_ISDRAW_R",
          label: "回覆是否參加",
        },
        {
          key: "AP_DATE",
          label: "報名時間",
        },
        {
          key: "AP_REPLYDATE",
          label: "中籤回復時間",
        },
        {
          key: "AP_AVAILABLENUM",
          label: "備取序號",
        },
      ],
      selected1: null,
      selected2: null,
      options1: [
        { value: null, text: "全部" },
        { value: 0, text: "否" },
        { value: 1, text: "正取" },
        { value: 2, text: "備取" },
        { value: 3, text: "正取未回覆" },
      ],
      options2: [
        { value: null, text: "全部" },
        { value: 0, text: "否" },
        { value: 1, text: "是" },
      ],
      isLoading: false,
      applyList: [],
      applyListCount:0,
      orignApplyList: [],
      apiGetApply: (data) =>
        this.userRequest.post("API/Default/GetApply", data),
    };
  },
  created() {
    this.userSignCheck();
    this.FunctionToken(this.FunctionGetApply, {});
  },
  computed: {
    rows() {
      return this.applyList.length;
    },
  },
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    getDrawLotsList() {
      this.applyList = [];
      for (let i in this.orignApplyList) {
        let subitm = this.orignApplyList[i];
        let count = 0;
        if (this.selected1 == null || subitm.AP_ISDRAW == this.selected1)
          count++;
        if (this.selected2 == null || subitm.AP_ISDRAW_R == this.selected2)
          count++;
        if (count == 2) this.applyList.push(subitm);
      }
      this.applyListCount = this.applyList.length;
    },
    //轉換時間
    parsetime(date_obj) {
      // console.log(date_obj);
      if (date_obj != null) {
        return date_obj.split("T").join(" ");
      } else {
        return "";
      }
    },
    //api
    FunctionGetApply(data) {
      this.isLoading = true,
        this.apiGetApply(data)
          .then((res) => {
            let json = JSON.parse(res.data);
            if (json.Status) {
              this.isLoading = false;
              let data = JSON.parse(json.Data);
              this.applyList = data;
              this.orignApplyList = data;
              this.applyListCount = data.length;
            }
          })
          .catch((err) => {
            console.log(err);
          });
    },
  },
};
</script>