<template>
  <b-container fluid>
    <h2 class="unit-title">匯入註記名單介面</h2>
    <b-container>
      <b-form class="otherForm increase-block mt-5">
        <b-row>
          <b-col>
            <h5 style="color: red">*請將身分證字號依序填入Excel中</h5>
            <div>上傳檔案格式限定為.csv、.xlsx、.xls</div>
            <div class="row">
              <div style="font-size: 18px" class="pr-3">Example:</div>
              <b-table-simple class="col-sm-1">
                <b-thead>
                  <b-tr>
                    <b-th>身分證字號</b-th>
                  </b-tr>
                </b-thead>
                <b-tbody>
                  <b-tr>
                    <b-td>A123456789</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>B123456789</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>C123456789</b-td>
                  </b-tr>
                  <b-tr>
                    <b-td>D123456789</b-td>
                  </b-tr>
                </b-tbody>
              </b-table-simple>
            </div>
            <input type="file" @change="onChange" accept=".csv,.xlsx,.xls" />
            <xlsx-read :file="file">
              <xlsx-json @parsed="getSheet"> </xlsx-json>
            </xlsx-read>
            <b-row>
              <b-col offset-lg="2" class="py-3 ml-auto">
                <b-button variant="primary" size="lg" @click="Import"
                  >確定匯入</b-button
                >
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
    </b-container>
  </b-container>
</template>

<script>
import { XlsxRead, XlsxJson } from "vue-xlsx";
export default {
  components: {
    XlsxRead,
    XlsxJson,
  },
  data() {
    return {
      file: null,
      collection: [],
      idNum: [], //身分證號
      errorText: "",
      apiMarkApply: (data) =>
        this.userRequest.post("API/Default/MarkApply", data),
    };
  },
  created() {
    this.userSignCheck();
  },
  computed: {},
  methods: {
    userSignCheck() {
      if (sessionStorage.getItem("TokenID") === null) {
        alert("請登入系統!!");
        this.$router.push({ path: "/Login" });
        // console.log("success");
      }
    },
    onChange(event) {
      this.idNum = [];
      this.errorText = "";
      this.file = event.target.files ? event.target.files[0] : null;
    },
    getSheet(val) {
      this.collection = val;
      for (let i = 0; i < this.collection.length; i++) {
        this.idNum.push(this.collection[i].身分證字號);
        if (this.collection[i].身分證字號.length != 10) {
          this.errorText += "第" + i + "筆身分證號有誤\n";
        }
      }
      console.log(this.idNum);
      if (this.errorText !== "") alert(this.errorText);
    },
    Import() {
      if (this.errorText === "") {
        // let json1 = {};
        // json1.NE_TITLE = this.form1.data["NE_TITLE"].val;

        // this.FunctionToken(this.FunctionAddNews, json1);

        // let AP_MARK = [];
        // AP_MARK = testfile;

        this.FunctionToken(this.FunctionMarkApply, { AP_NID: this.idNum });
      }
    },
    //api

    FunctionMarkApply(data_in) {
      this.apiMarkApply(data_in)
        .then((res) => {
          let json = JSON.parse(res.data);
          let failNID = JSON.parse(json.Data);
          console.log("FunctionMarkApply");
          console.log(failNID);
          if (json.Status) {
            if (failNID.length != 0) {
              alert("無以下身分證號\n"+failNID);
            } else alert("註記成功!");
            this.$router.go();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>