<template>
  <div class="top-bar mb-4">
    <div class="top-bar-left">
      <ul class="dropdown menu" data-dropdown-menu>
        <li class="site-name">國慶全球資訊網後台管理系統</li>
        <li>
          <a href="#" @click.prevent="goAdminIndex">最新消息</a>
        </li>
        <li>
          <a href="#" @click.prevent="goAdminApply">報名人員</a>
        </li>
        <li>
          <a href="#" @click.prevent="goAdminImport">匯入註記名單</a>
        </li>
        <li>
          <a href="#" @click.prevent="goAdminDrawLots">抽籤</a>
        </li>
        <li>
          <a href="#" target="_blank" @click.prevent="goAdminTimeSet">時間設定</a>
        </li>
        <li>
          <a href="#" target="_blank" @click.prevent="goAdminSendMail">發送郵件</a>
        </li>
        <li>
          <a href="#" target="_blank" @click.prevent="goAdminMailList">已寄送郵件清單</a>
        </li>
      </ul>
    </div>
    <div class="top-bar-right">
      <ul class="dropdown menu" data-dropdown-menu>
        <li>
          <a href="#" @click.prevent="goFront">前台</a>
        </li>
        <li>
          <a href="#" @click.prevent="signout">登出</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "HeadMenuAdmin",
  data() {
    return {
      MyAccount: {
        AC_CONTENT: null,
        AC_GROUP_ID: null,
        AC_TEL: null,
        AC_TITLE: null,
        AC_USERID: null,
        AC_USERNAME: null,
        AG_NAME: null,
        AC_TEL_EX: null,
      },
    };
  },
  created() {},
  methods: {
    goAdminIndex() {
      this.$router.push({ path: "/Admin_Index" });
    },
    goAdminApply() {
      this.$router.push({ path: "/Admin_Apply" });
    },
    goAdminImport() {
      this.$router.push({ path: "/Admin_Import_Apply" });
    },
    goAdminDrawLots() {
      this.$router.push({ path: "/Admin_DrawLots" });
    },
    goAdminTimeSet() {
      this.$router.push({ path: "/Admin_TimeSet" });
    },
    goAdminSendMail(){
      this.$router.push({ path: "/Admin_SendMail" });
    },
    goAdminMailList(){
      this.$router.push({ path: "/Admin_MailList" });
    },
    goFront() {
      this.$router.push({ path: "/" });
    },
    signout() {
      sessionStorage.removeItem("TokenID");
      this.TokenID = "";
      this.$router.push({ path: "/Login" });
    }
  },
};
</script>